.Searchbar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 50%;
  width: 450px;
  transform: translateX(-50%);
  z-index: 1100;
  min-height: 64px;
  padding: 15px 24px;
  color: #fff;
}

.SearchForm {
  display: flex;
  width: auto;
  align-items: center;
  background: #bdb7b7e3;
  max-width: 600px;
  border-radius: 10px;
  overflow: hidden;
  margin: 0 auto;
}

.SearchFormInput {
  display: block;
  width: 100%;
  font: inherit;
  font-size: 20px;
  color: #a8a8a8;
  padding: 5px;
  padding-left: 10px;
  border-radius: 10px;
  outline: none;
  background-color: inherit;
}

.SearchFormInput::placeholder {
  font: inherit;
  font-size: 18px;
}
